import axios, { routePathAPI, routePathAPIVersion } from '@/plugins/axios.js'
import axiosOrder, { routePathOrderAPI } from '@/plugins/axios-order.js'

const getHeaders = (initial = {}) => {
  const headers = initial

  const guestId = localStorage.getItem('novidin.guestId') || null
  if (guestId !== null) {
    headers['x-guest-id'] = guestId
  }

  const tokenType = localStorage.getItem('novidin.tokenType') || 'Bearer'
  const accessToken = localStorage.getItem('novidin.accessToken') || null
  if (accessToken !== null) {
    headers['Authorization'] = `${tokenType} ${accessToken}`
  }

  const config = {
    headers,
  }

  return config
}

// initial state
const state = () => ({
  lastTransaction: {},
  totalActiveOrder: 0,
  activePage: 'active', // active || history
})

// getters
const getters = {
  lastTransactionGetter(state) {
    return state.lastTransaction || {}
  },
  totalActiveOrder(state) {
    return state.totalActiveOrder || 0
  },
  activePage(state) {
    return state.activePage
  },
  lastTrancactionId() {
    return localStorage.getItem('novidin.lastTrancactionId') || null
  },
}

