<script>
import globalMixin from '@/mixins/global.js'
import {
  Pagination,
  Mousewheel,
  Autoplay,
  FreeMode,
  Lazy,
  Scrollbar,
} from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/vue'
import FsLightbox from 'fslightbox-vue/v3'

import Multiselect from '@vueform/multiselect'
import AddToCalendar from '@/components/AddToCalendar.vue'
import Button from '@/components/button/Button.vue'
import {
  PlayIcon,
  PauseIcon,
  MailIcon,
  CalendarIcon,
  ClipboardCopyIcon,
  GiftIcon,
} from '@heroicons/vue/solid'
import ModalBasic from '@/components/modal/ModalBasic.vue'
import BottomNavBar from '@/components/navbar/BottomNavBar.vue'
import backgroundMusic from '@/assets/music/Until I Found You - Stephen Sanchez - Cover (Violin).mp3'
import prewedOne from '@/assets/image/person/contoh1.jpeg'
import prewedTwo from '@/assets/image/person/contoh1.jpeg'
import prewedThree from '@/assets/image/person/contoh1.jpeg'
import prewedFour from '@/assets/image/person/contoh1.jpeg'
import prewedFive from '@/assets/image/person/contoh1.jpeg'

export default {
  name: 'IndexPage',
  components: {
    Swiper,
    SwiperSlide,
    Multiselect,
    FsLightbox,
    BottomNavBar,
    Button,
    ModalBasic,
    AddToCalendar,
    PlayIcon,
    PauseIcon,
    MailIcon,
    CalendarIcon,
    ClipboardCopyIcon,
    GiftIcon,
  },
  mixins: [globalMixin],
  inject: ['deviceWidth'],
  setup(props, { emit }) {
    const modules = [Autoplay, Pagination]
    const modulesFree = [Scrollbar, Mousewheel, Lazy, FreeMode]

    return {
      modules,
      modulesFree,
    }
  },
  data: () => ({
    search: '',
    welcome: false,
    played: false,
    backgroundMusic,
    audio: new Audio(backgroundMusic),
    toggler: false,
    prewedIndex: 0,
    galleryPrewed: [prewedOne, prewedTwo, prewedThree, prewedFour, prewedFive],
    activeMenu: '',
    activeMenuIndex: 1,
    lastActiveMenuId: 1,
    menuActiveClasses: 'text-ink-primary',
    menuNotActiveClasses: 'text-gray-500',
    husband: {
      code: 'DAYAH',
      couple: {
        nickname: 'Dayah & Lucky',
        fullName: 'Hidayahtullah & Lucky Zuli Zahara',
        brideOne: {
          nickname: 'Dayah',
          fullName: 'Hidayahtullah',
          parent: 'Putra ke-1 dari Kel. Bpk Hotib & Ibu Nining',
        },
        brideTwo: {
          nickname: 'Lucky',
          fullName: 'Lucky Zuli Zahara',
          parent: 'Putri ke-5 dari Kel. Bpk Ruddy Nasrullah & Ibu Andi tri ekiwidianti',
        },
      },
      date: '07.16.2023',
      dateString: '16 Juli 2023',
      hourString: '09.00 - 22.00',
    },
    wife: {
      code: 'LUCKY',
      couple: {
        nickname: 'Lucky & Dayah',
        fullName: 'Lucky Zuli Zahara & Hidayahtullah',
        brideOne: {
          nickname: 'Lucky',
          fullName: 'Lucky Zuli Zahara',
          parent: 'Putri ke-5 dari Kel. Bpk Ruddy Nasrullah & Ibu Andi tri ekiwidianti',
        },
        brideTwo: {
          nickname: 'Dayah',
          fullName: 'Hidayahtullah S.Kom',
          parent: 'Putra ke-1 dari Kel. Bpk Hotib & Ibu Nining',
        },
      },
      date: '07.16.2023',
      dateString: '16 Juli 2023',
      hourString: '09.00 - Selesai',
    },
    akad: {
      date: '07.16.2023',
      dateString: '16 Juli 2023',
      hourString: '09.00 - Selesai',
    },
    bride: {
      code: null,
      couple: {
        nickname: null,
        fullName: null,
        brideOne: {
          nickname: null,
          fullName: null,
          parent: null,
        },
        brideTwo: {
          nickname: null,
          fullName: null,
          parent: null,
        },
      },
      date: new Date('08/17/2045'),
      dateString: null,
    },
    visitor: {
      name: 'Numa Raynatul & Partner',
      address: 'Planet Mars',
    },
  }),
  mounted() {
    this.checkingCode()
  },
  methods: {
    checkingCode() {
      const code = this.$route.params.code
      const to = this.$route.query.to
      const address = this.$route.query.address
      if (code === 'day') {
        this.bride = this.husband

        this.visitor = {
          name: to,
          address: address ?? '',
        }

        this.welcome = true
      } else if (code === 'lucky') {
        this.bride = this.wife

        this.visitor = {
          name: to,
          address: address ?? '',
        }

        this.welcome = true
      } else {
        this.$router.push({
          name: 'index',
          query: {
            message: 'Sepertinya kode yang anda masukan salah',
          },
        })
      }
    },
    openInvitation() {
      this.welcome = false
      this.audio.loop = true

      const activeElement =
        this.$refs['bottomNavBar'].$refs['menu-' + this.activeMenuIndex]

      activeElement.classList.value = activeElement.classList.value.replace(
        this.menuNotActiveClasses,
        this.menuActiveClasses
      )
      activeElement.classList.add('animate__animated', 'animate__tada')

      this.play()
    },
    play() {
      if (this.played === false) {
        this.played = true
        this.audio.play()
      } else {
        this.played = false
        this.audio.pause()
      }
    },
    selectMenu(event, scrolled) {
      if (this.activeMenuIndex !== event.index) {
        this.activeMenuIndex = event.index

        const activeElement =
          this.$refs['bottomNavBar'].$refs['menu-' + this.activeMenuIndex]
        const lastActiveElement =
          this.$refs['bottomNavBar'].$refs['menu-' + this.lastActiveMenuId]

        activeElement.classList.value = activeElement.classList.value.replace(
          this.menuNotActiveClasses,
          this.menuActiveClasses
        )
        activeElement.classList.add('animate__animated', 'animate__tada')

        lastActiveElement.classList.value =
          lastActiveElement.classList.value.replace(
            this.menuActiveClasses,
            this.menuNotActiveClasses
          )
        lastActiveElement.classList.remove('animate__animated', 'animate__tada')

        this.lastActiveMenuId = this.activeMenuIndex

        // const element = document.querySelectorAll(`.${event.menu}-animating`)
        // if (element !== null) {
        //   for (var i = 0; i < element.length; ++i) {
        //     element[i].classList.add(
        //       'animate__animated',
        //       'animate__fadeInUp',
        //       'animate__slow'
        //     )
        //   }
        // }

        if (scrolled !== false) {
          document
            .getElementById(event.menu)
            .scrollIntoView({ behavior: 'smooth' })
        }
      }
    },
    onSwiper(swiper) {
      console.log(swiper)
    },
    onSlideChange() {
      console.log('slide change')
    },
    openFullPreview(index) {
      this.toggler = !this.toggler
      this.prewedIndex = index
    },
    onHide() {
      this.toggler = false
    },
  },
}
</script>
