      return `${name}  [${language}]`
    }

    const { locale } = useI18n()

    const addItemToCart = () => {
      if (!itemName.value) return
      cart.addItem(itemName.value)
      itemName.value = ''
    }

    const clearCart = () => {
      if (window.confirm('Are you sure you want to clear the cart?')) {
        cart.rawItems = []
      }
    }

    const buy = async () => {
      const n = await cart.purchaseItems()

      console.log(`Bought ${n} items`)

      cart.rawItems = []
    }

    const setIsModalOpen = (value) => {
      isModalOpen.value = value
    }

    const onSwiper = (swiper) => {
      console.log(swiper)
    }
    const onSlideChange = () => {
      console.log('slide change')
    }

    const closeModal = () => {
      isModalOpen.value = false
    }
    const openModal = () => {
      isModalOpen.value = true
    }

    // const emit = defineEmits([
    //   'showGlobalModalMaintenance',
    //   'showGlobalNotAuthenticated',
    // ])

    const showGlobalModalMaintenance = () => {
      emit('showGlobalModalMaintenance', true)
    }

    const showGlobalNotAuthenticated = () => {
      emit('showGlobalNotAuthenticated', true)
    }

    const showGlobalMesageAlert = () => {
      emit('show-global-message-alert', {
        show: true,
        message: 'message',
        type: 'error',
      })
    }

    return {
      modules,
      modulesFree,
      formattedDate,
      value,
      selectedPerson,
      itemName,
      isOpen,
      isModalOpen,
      nameWithLang,
      user,
      cart,
      multiSelectOptions,
      people,
      locale,
      addItemToCart,
      clearCart,
      buy,
      setIsModalOpen,
      onSwiper,
      onSlideChange,
      closeModal,
      openModal,
      showGlobalModalMaintenance,
      showGlobalNotAuthenticated,
      showGlobalMesageAlert,
    }
  },
})
</script>
